  // to get proper task name
  function checkTxTask(task) {
    if (task == "wallet_creation") {
      return "Wallet Creation";
    } else if (task == "claim") {
      return "Claim";
    } else if (task == "purcahse_miner") {
      return "Purchase Miner";
    } else if (task == "stake_miner") {
      return "Stake Miner";
    } else if (task == "unstake_miner") {
      return "Unstake Miner";
    } else if (task == "transfer_miner") {
      return "Transfer Miner";
    } else if (task == "delete_miner") {
      return "Delete Miner";
    } else if (task == "withdraw_bnb") {
      return "BNB Withdraw";
    } else if (task == "withdraw_token") {
      return "Token Withdraw";
    } else if (task == "gift_miner") {
      return "Giveaway";
    }
  }

  // to check notification time difference
  function checkNotiTime(prtime) {
    var currentime = new Date();
    let createdTasktime = new Date(prtime);
    const convertedDate = new Date(
      createdTasktime.getTime() -
        createdTasktime.getTimezoneOffset() * 60 * 1000
    );
    var diff = currentime - convertedDate;
    var msec = diff;
    var dd = Math.floor(msec / 1000 / 60 / 60 / 24);
    msec -= dd * 1000 * 60 * 60 * 24;
    var hh = Math.floor(msec / 1000 / 60 / 60);
    msec -= hh * 1000 * 60 * 60;
    var mm = Math.floor(msec / 1000 / 60);
    msec -= mm * 1000 * 60;
    var ss = Math.floor(msec / 1000);
    msec -= ss * 1000;
    if (dd < 1) {
      if (hh < 1) {
        return mm + " mins ago";
      } else if (hh == 1) {
        return hh + " hour " + mm + " mins ago";
      } else {
        return hh + " hours " + mm + " mins ago";
      }
    } else if (dd == 1) {
      return dd + " day " + hh + " hours ago";
    } else if (dd > 1) {
      return dd + " days " + hh + " hours ago";
    }
  }

