import React from "react";
import dataVal from "./data/dataval.json";

export default function Footer() {
  const EGOLD_ADDRESS = dataVal.egoldtokencontract;

  async function addEgoldTokenFunction(add) {
    try {
      const wasAdded = await window.ethereum.request({
        method: "wallet_watchAsset",
        params: {
          type: "ERC20",
          options: {
            address: add,
            symbol: "EGOLD",
            decimals: 18,
            image: "https://egold.farm/favicon-96x96.png",
          },
        },
      });

      if (wasAdded) {
        console.log("Thanks for your interest!");
      } else {
        console.log("EGOLD  has not been added");
      }
    } catch (error) {
      console.log(error);
    }
  }

  return (
    <div
      className="flxrow"
      style={{
        paddingBottom: "50px",
        textAlign: "center",
        maxWidth: "90%",
        marginTop: "50px",
      }}
    >
      <section
        className="fieldset"
        style={{
          display: "inline-block",
          borderRadius: "11px",
          backgroundColor: "#42425d",
          marginTop: "0px",
          margin: "0 auto",
        }}
      >
        <h1>EGOLD</h1>
        <p
          style={{
            fontSize: "14px",
            letterSpacing: "1px",
            lineHeight: "19px",
            color: "#ffffff",
            textAlign: "center",
            fontWeight: 500,
            textTransform: "none",
            marginTop: "12px",
            wordBreak: "break-word",
            cursor: "pointer",
          }}
          onClick={() => {
            navigator.clipboard.writeText(EGOLD_ADDRESS);
          }}
          className="graveyard"
        >
          {EGOLD_ADDRESS}
        </p>
        <button
          className="graveyard copyaddr"
          style={{
            cursor: "pointer",
            backgroundColor: "rgb(46 46 59)",
            float: "left",
            fontWeight: 600,
            fontSize: "12px",
          }}
          onClick={() => {
            navigator.clipboard.writeText(EGOLD_ADDRESS);
          }}
        >
          COPY
        </button>
        <button
          className="graveyard addtometalanding"
          style={{
            cursor: "pointer",
            backgroundColor: "rgb(46 46 59)",
            float: "right",
            fontWeight: 600,
            fontSize: "12px",
          }}
          onClick={() => {
            addEgoldTokenFunction(EGOLD_ADDRESS);
          }}
        >
          ADD TO METAMASK
        </button>
      </section>
    </div>
  );
}
