import React, { useState, useEffect } from 'react'
import EgoldIcon from "./images/egoldIconn.png"
import Img1 from "./images/Img1.png"
import Img2 from "./images/Img2.png"
import Img3 from "./images/Img3.png"
import bnbIcon from "./images/bnbicon.png"
import wethlogo from "./images/weth-logo.png"
import tagicon from "./images/tagicon.png"
import Header from "./Header";
import Loader from "./Loader/Loader";
import { useNavigate, useLocation, Link } from "react-router-dom";
import {
    Card,
    CardBody,
    CardTitle
} from "reactstrap";
import dataVal from "./data/dataval.json";
import { IoMdShareAlt } from "react-icons/io";
var STAKEFARM_ABIJSON = require("./data/stakefarm.json");
var STAKENAME_ABIJSON = require("./data/stakeName.json");
const EGOLD_ADDRESS = dataVal.egoldtokencontract;
const TOKEN_ABI = dataVal.busdtokenabi;
export default function MainLanding() {
    let history = useNavigate();
    const [loader, setLoader] = useState(true);
    const [Listitems, setListitems] = useState({ data: [], status: false })
    const [farmList, setfarmData] = useState("initial")
    const Web3 = require("web3");
    const web3 = new Web3(process.env.REACT_APP_RPC);

    useEffect(() => {
        fetchData()
    }, [])
    async function fetchData() {
        console.log("INSIDE 1")
        const url = "https://auth.egoldfarm.com/getfarms"
        let previousList = []
        const egoldcontractInstance = new web3.eth.Contract(
            TOKEN_ABI,
            EGOLD_ADDRESS
        );
        fetch(url)
            .then(function (response) {
                return response.json();
            })
            .then(function (farmdata) {
                setfarmData(farmdata.data)

                farmdata.data.map(async (items, index) => {
                    const farmContractInstance = new web3.eth.Contract(
                        STAKEFARM_ABIJSON,
                        items._id
                    );
                    farmContractInstance.methods
                        .erc20()
                        .call(async function (err, res1) {
                            const farmContractNameInstance = new web3.eth.Contract(
                                STAKENAME_ABIJSON,
                                res1
                            );
                            farmContractNameInstance.methods
                                .name()
                                .call(async function (err, res) {
                                    console.log("NAME RES", res)
                                    farmContractInstance.methods
                                        .endBlock()
                                        .call(async function (err, endBres) {
                                            farmContractNameInstance.methods
                                                .balanceOf(items._id)
                                                .call(async function (err, resTKO) {
                                                    console.log("NAME RES", resTKO)
                                                    egoldcontractInstance.methods
                                                        .balanceOf(items._id)
                                                        .call({}, function (e, resEgold) {
                                                            let formattedData = setDataformat(res, index, items, endBres, resTKO, resEgold)
                                                            previousList.push(formattedData)
                                                            console.log("PREVOISU", previousList)
                                                            console.log("PREVOISU", previousList)
                                                            setListitems({ data: previousList, status: true })
                                                        });

                                                })
                                        })
                                });
                        });
                })
            })

            .catch(function (error) {
                console.log("Requestfailed", error);
            });


    }
    function setDataformat(name, index, apiData, endBres, resTKO, resEgold) {
        let data = {
            id: apiData._id,
            tokname: name,
            tokens: parseFloat(web3.utils.fromWei(resTKO)).toFixed(2),
            participants: apiData.participants,
            egoldstkd: parseFloat(web3.utils.fromWei(resEgold)).toFixed(8),
            apy: "25.76",
            endblk: endBres
        }
        return (data)
    }
    return (
        <>
            <Header />
            <div className='page-content'>
                <div className='col-sm-12' style={{ display: "flex", justifyContent: "center", alignItems: "center", gap: "2%", marginTop: 50, flexWrap: "wrap" }}>
                    <span style={{ border: "2px solid #545488", borderRadius: 5, marginTop: 4 }}>
                        <p style={{ color: "white", padding: "4px 30px", margin: "0px", fontWeight: "700", color: "#C5CDE4", fontSize: "13px" }} >

                            ACTIVE
                        </p>
                    </span>
                    <span style={{ border: "2px solid #545488", borderRadius: 5, backgroundColor: "#6969AE", cursor: "pointer", marginTop: 4 }}>
                        <p style={{ color: "white", padding: "4px 30px", margin: "0px", fontWeight: "700", color: "#C5CDE4", fontSize: "13px" }} >
                            UPCOMING
                        </p>
                    </span>
                    <span style={{ border: "2px solid #545488", borderRadius: 5, backgroundColor: "#6969AE", cursor: "pointer", marginTop: 4 }}>
                        <p style={{ color: "white", padding: "4px 30px", margin: "0px", fontWeight: "700", color: "#C5CDE4", fontSize: "13px", cursor: "pointer" }} >
                            CLOSED
                        </p>
                    </span>
                    <span style={{ border: "2px solid #545488", borderRadius: 5, backgroundColor: "#6969AE", cursor: "pointer", marginTop: 4 }}>
                        <p style={{ color: "white", padding: "4px 30px", margin: "0px", fontWeight: "700", color: "#C5CDE4", fontSize: "13px" }} >
                            TRACK STAKING
                        </p>
                    </span>
                </div>
                <div className='col-sm-12 listbox' >
                    {console.log("LENGTH", Listitems.length)}
                    {

                        Listitems.status ?
                            Listitems.data.map((dataitem, index) => {
                                console.log("ITEMSSS ", dataitem)
                                return (
                                    <div key={index} style={{ marginTop: 25 }}>
                                        <Card
                                            style={{
                                                width: '18rem'
                                            }}
                                        >
                                            <CardBody>
                                                <CardTitle style={{
                                                    color: "white", textAlign: "center", padding: 10, backgroundColor: "rgb(43 43 73)", borderTopRightRadius: 7, borderTopLeftRadius: 7, fontFamily: "sans-serif",
                                                    fontSize: 15,
                                                    fontWeight: "600",
                                                    color: "#C5CDE4", fontSize: "13px",
                                                    justifyContent: "center",
                                                    alignItems: "center",
                                                    display: "flex"
                                                }}>
                                                    <img
                                                        alt="Card cap"
                                                        src={bnbIcon}
                                                        width={30}
                                                        height={30}
                                                        style={{ marginRight: 10 }}
                                                    />
                                                    <p style={{ margin: 0 }}>
                                                        {dataitem.tokname}
                                                    </p>

                                                </CardTitle>
                                            </CardBody>
                                            <img
                                                alt="Card cap"
                                                src={Img1}
                                                height={250}
                                            />
                                            <div style={{ display: "flex", justifyContent: "center", alignItems: "center", backgroundColor: "#272b65" }}>
                                                <span style={{
                                                    color: "rgb(215 216 220)",
                                                    padding: 10,
                                                    fontSize: 13,
                                                    fontFamily: "sans-serif",
                                                    fontWeight: 600
                                                }}>
                                                    VISIT PROJECT WEBSITE
                                                </span>
                                            </div>
                                            <ul
                                                className="accinfo-list"
                                                style={{ marginTop: 3, background: "#363657" }}
                                            >
                                                <li>
                                                    <label style={{ color: "#fff", fontSize: 11, }}>
                                                        TOKENS OFFERED
                                                    </label>
                                                    <span style={{ textAlign: "right" }}>
                                                        {dataitem.tokens}
                                                    </span>
                                                </li>
                                                <li>
                                                    <label style={{ color: "#fff", fontSize: 11, }}>
                                                        PARTICIPANTS
                                                    </label>
                                                    <span style={{ textAlign: "right", fontSize: 11, }}>
                                                        {dataitem.participants}
                                                    </span>
                                                </li>
                                                <li>
                                                    <label style={{ color: "#fff", fontSize: 11, }}>
                                                        EGOLD STAKED
                                                    </label>
                                                    <span style={{ textAlign: "right" }}>
                                                        {dataitem.egoldstkd}
                                                    </span>
                                                </li>
                                                <li>
                                                    <label style={{ color: "#fff", fontSize: 11, }}>
                                                        APY
                                                    </label>
                                                    <span style={{ textAlign: "right" }}>
                                                        {dataitem.apy}
                                                    </span>
                                                </li>
                                                <li>
                                                    <label style={{ color: "#fff", fontSize: 11, }}>
                                                        END BLOCK
                                                    </label>
                                                    <span style={{ textAlign: "right" }}>
                                                        {dataitem.endblk}
                                                    </span>
                                                </li>

                                            </ul>
                                            <div style={{ position: "relative", display: "flex", justifyContent: "center", alignItems: "center", backgroundColor: "rgb(33 29 71)" }}  >
                                                <span style={{
                                                    color: "rgb(215 216 220)",
                                                    padding: 12,
                                                    fontSize: 13,
                                                    fontFamily: "sans-serif",
                                                    fontWeight: 600,
                                                    justifyContent: "center",
                                                    alignItems: "center",
                                                    display: "flex",
                                                    cursor: "pointer"
                                                }}
                                                // onClick={() => {
                                                //     history("/stake/" + dataitem.id);
                                                // }}
                                                >
                                                    <Link to="/stake" state={{ data: dataitem }} className="link" style={{ color: "#D7D8DC" }}>
                                                        STAKE EGOLD
                                                    </Link>

                                                </span>
                                                <IoMdShareAlt color='white' size={25} style={{ position: "absolute", right: 10 }} />
                                            </div>
                                        </Card>
                                    </div>
                                )
                            }) : <Loader />
                    }
                </div>
            </div>
        </>
    )
}
