import React, { useState, useEffect } from "react";
import "../css/dashboard.css";
import Header from "../Header";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  Row,
  Col,
  Input,
  Form,
  FormGroup,
  ModalFooter,
  Label,
  Container,
} from "reactstrap";
import ls from "local-storage";
import { useNavigate } from "react-router-dom";
import { useNotiStore } from "../Store/store";
import {
  IoArrowBackCircleOutline,
  IoClipboardOutline,
  IoEyeOutline,
} from "react-icons/io5";

export default function Changepw() {
  let history = useNavigate();
  const status = ls.get("twoFaStatus");
  const [copied, setCopied] = useState(false);
  // const status = false
  const [password, setPassword] = useState("");
  const [newpassword, setnewPassword] = useState("");
  const [conpassword, setconPassword] = useState("");
  const [errorMSG, seterrorMSG] = useState({ status: false, message: "" });
  const [successMSG, setsuccessMSG] = useState({ status: false, message: "" });
  const appDisconnect = useNotiStore((state) => state.appDisconnect);
  const [OTP, setOTP] = useState("");

  useEffect(() => {
    const token = localStorage.getItem("accessToken");
    if (token) {
      if (Date.now() - parseInt(localStorage.getItem("accessTime")) > 7200000) {
        localStorage.clear();
        appDisconnect();
      }
    }
    const interval = setInterval(() => {
      if (token) {
        if (
          Date.now() - parseInt(localStorage.getItem("accessTime")) >
          7200000
        ) {
          localStorage.clear();
          appDisconnect();
        }
      }
    }, 10000);

    return () => clearInterval(interval);
  }, []);

  async function ChangePassword() {
    const token = localStorage.getItem("accessToken");
    setsuccessMSG({ status: false });
    if (password.length && newpassword.length && conpassword.length > 0) {
      seterrorMSG({ status: false });
      const data = {
        password: password,
        newpassword: newpassword,
        newpwconfirm: conpassword,
        totp: status ? OTP : "",
      };

      fetch("https://auth.egoldfarm.com/api/auth/changepw", {
        method: "PUT",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: "Bearer " + String(token),
        },
        body: JSON.stringify(data),
      })
        .then((response) => response.json())
        .then((json) => {
          if (json.status == "success") {
            setsuccessMSG({
              status: true,
              message: "Password changed successfully",
            });
            setPassword("");
            setconPassword("");
            setnewPassword("");
            setOTP("");
          } else {
            if (json.message)
              seterrorMSG({ status: true, message: json.message });
            else seterrorMSG({ status: true, message: json.detail });
          }
        })
        .catch((error) => {
          console.error(error);
        });
    } else {
      seterrorMSG({ status: true, message: "Please enter details" });
    }
  }

  return (
    <>
      <Header />
      <div className="container">
        <section
          className="profilebody mb-5"
          style={{ backgroundColor: "rgb(37, 37, 47)", borderRadius: "14px" }}
        >
          <div className="container pb-5">
            <div className="row">
              <div className="col">
                <nav
                  aria-label="breadcrumb"
                  className="rounded-3 p-3 mb-4"
                  style={{
                    backgroundColor: "#1c1c23",
                    marginTop: "15px",
                    boxShadow: "0px 1px 8px 5px rgb(22 22 26 / 31%)",
                  }}
                >
                  <a
                    style={{ cursor: "pointer" }}
                    onClick={() => {
                      history("/");
                    }}
                  >
                    <IoArrowBackCircleOutline
                      color="white"
                      style={{ marginRight: 6, marginBottom: 2 }}
                    />
                  </a>
                  <ol
                    className="breadcrumb mb-0"
                    style={{ display: "inline-flex" }}
                  >
                    <li className="breadcrumb-item inactive">
                      <a
                        onClick={() => {
                          history("/");
                        }}
                      >
                        Home
                      </a>
                    </li>
                    <li className="breadcrumb-item active" aria-current="page">
                      Profile
                    </li>
                  </ol>
                </nav>
              </div>
            </div>
            <div className="row">
              <div className="col-lg-4">
                <div className="card profilecard mb-4">
                  <div className="card-body text-center ">
                    {/* <img
                    src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-chat/ava3.webp"
                    alt="avatar"
                    className="rounded-circle img-fluid"
                    style={{ width: "150px" }}
                  /> */}
                    <p className="useradd mb-1">{ls.get("acct")}</p>
                    <div
                      className="d-flex justify-content-center mb-2"
                      style={{ display: "flex" }}
                    >
                      <span style={{ width: "50%" }}>
                        <button
                          type="button"
                          className="btn"
                          style={{
                            color: "white",
                            fontSize: "13px",
                            marginTop: "2px",
                          }}
                          onClick={async () => {
                            await navigator.clipboard.writeText(ls.get("acct"));
                            setCopied(true);
                            setTimeout(function () {
                              setCopied(false);
                            }, 2000);
                          }}
                        >
                          <IoClipboardOutline
                            color="white"
                            style={{ marginRight: 6, marginBottom: 2 }}
                          />
                          {copied ? "Copied" : "Copy"}
                        </button>
                      </span>
                      <span style={{ width: "50%" }}>
                        <a
                          type="button"
                          className="btn"
                          style={{ color: "white", fontSize: "13px" }}
                          href={"https://bscscan.com/address/" + ls.get("acct")}
                          target="_blank"
                        >
                          <IoEyeOutline
                            color="white"
                            style={{ marginRight: 6, marginBottom: 2 }}
                          />
                          View on Bscscan
                        </a>
                      </span>
                    </div>
                  </div>
                </div>
                <div className="card profilecard mb-4 mb-lg-0">
                  <div className="card-body p-0">
                    <ul className="list-group list-group-flush">
                      <a
                        data-toggle="collapse"
                        href="#collapseExample"
                        role="button"
                        aria-expanded="false"
                        aria-controls="collapseExample"
                      >
                        <li
                          className="list-group-item d-flex justify-content-between align-items-center p-3"
                          style={{ borderRadius: "15px 15px 0px 0px" }}
                        >
                          <p className="mb-0">
                            <i
                              class="bi bi-person"
                              style={{ fontSize: "20px", paddingRight: "10px" }}
                            ></i>{" "}
                            PROFILE
                          </p>
                        </li>
                      </a>
                      <div class="collapse show" id="collapseExample">
                        <a
                          onClick={() => {
                            history("/profile");
                          }}
                        >
                          {" "}
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab">
                            <p className="mb-0">Overview</p>
                          </li>
                        </a>

                        {status ? null : (
                          <a
                            onClick={() => {
                              history("/enableauthdash");
                            }}
                          >
                            <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab ">
                              <p className="mb-0">Enable 2FA</p>
                            </li>
                          </a>
                        )}

                        <a
                          onClick={() => {
                            history("/security");
                          }}
                        >
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab">
                            <p className="mb-0">Security</p>
                          </li>
                        </a>
                        <a
                          onClick={() => {
                            history("/changepw");
                          }}
                        >
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab activetab">
                            <p className="mb-0">Change Password</p>
                          </li>
                        </a>
                      </div>
                      <a
                        onClick={() => {
                          history("/wallet");
                        }}
                      >
                        <li className="list-group-item d-flex justify-content-between align-items-center p-3">
                          <p className="mb-0">
                            <i
                              class="bi bi-wallet2"
                              style={{ fontSize: "20px", paddingRight: "10px" }}
                            ></i>{" "}
                            WALLET
                          </p>
                        </li>
                      </a>
                      <a
                        onClick={() => {
                          history("/transactions");
                        }}
                      >
                        <li
                          className="list-group-item d-flex justify-content-between align-items-center p-3"
                          style={{ borderRadius: "0px 0px 15px 15px" }}
                        >
                          <p className="mb-0">
                            {" "}
                            <i
                              class="bi bi-clock-history"
                              style={{ fontSize: "20px", paddingRight: "10px" }}
                            ></i>{" "}
                            TRANSACTION HISTORY
                          </p>
                        </li>
                      </a>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="col-lg-7" style={{ margin: "0 auto" }}>
                <div className="card profilecard mb-4">
                  <div className="card-body">
                    <div className="row" style={{ justifyContent: "center" }}>
                      {errorMSG.status ? (
                        <div
                          style={{ display: "flex", justifyContent: "center" }}
                        >
                          <p className="errorbox" style={{ width: "340px" }}>
                            {errorMSG.message}
                          </p>
                        </div>
                      ) : null}
                      {successMSG.status ? (
                        <div
                          style={{ display: "flex", justifyContent: "center" }}
                        >
                          <p className="successrbox" style={{ width: "340px" }}>
                            {successMSG.message}
                          </p>
                        </div>
                      ) : null}

                      <Form className="form changepwinputfields">
                        <FormGroup>
                          <Label
                            for="pw"
                            style={{
                              color: "white",
                              fontSize: "13px",
                            }}
                          >
                            Password
                          </Label>
                          <div className="iconpw">
                            <Input
                              type="password"
                              name="password"
                              value={password}
                              id="pw"
                              style={{
                                backgroundColor: "#24252F",
                                borderColor: "#5D5D97",
                                color: "white",
                              }}
                              placeholder="Current Password"
                              onChange={(password) => {
                                setPassword(password.target.value);
                              }}
                            />
                          </div>
                        </FormGroup>
                        <FormGroup>
                          <Label
                            for="newpw"
                            style={{
                              color: "white",
                              fontSize: "13px",
                            }}
                          >
                            New Password
                          </Label>
                          <div className="iconpw">
                            <Input
                              type="password"
                              name="password"
                              value={newpassword}
                              id="newpw"
                              style={{
                                backgroundColor: "#24252F",
                                borderColor: "#5D5D97",
                                color: "white",
                              }}
                              placeholder="New Password"
                              onChange={(newpw) => {
                                setnewPassword(newpw.target.value);
                              }}
                            />
                          </div>
                        </FormGroup>
                        <FormGroup>
                          <Label
                            for="conpw"
                            style={{
                              color: "white",
                              fontSize: "13px",
                            }}
                          >
                            Confirm Password
                          </Label>
                          <div className="iconpw">
                            <Input
                              type="password"
                              name="password"
                              value={conpassword}
                              id="conpw"
                              style={{
                                backgroundColor: "#24252F",
                                borderColor: "#5D5D97",
                                color: "white",
                              }}
                              placeholder="Confirm New Password"
                              onChange={(conpw) => {
                                setconPassword(conpw.target.value);
                              }}
                            />
                          </div>
                        </FormGroup>
                        {status ? (
                          <FormGroup>
                            <Label
                              for="code"
                              style={{
                                color: "white",
                                fontSize: "13px",
                              }}
                            >
                              2FA Code
                            </Label>
                            <div className="iconpw">
                              <Input
                                type="text"
                                name="code"
                                value={OTP}
                                id="code"
                                style={{
                                  backgroundColor: "#24252F",
                                  borderColor: "#5D5D97",
                                  color: "white",
                                }}
                                placeholder="2FA OTP"
                                onChange={(otp) => {
                                  setOTP(otp.target.value);
                                }}
                              />
                            </div>
                          </FormGroup>
                        ) : null}

                        <div className="changepw">
                          <Button
                            size="sm"
                            style={{
                              backgroundColor: "rgb(92, 64, 128)",
                              padding: "10px",
                              borderRadius: "0.25rem",
                            }}
                            onClick={() => {
                              ChangePassword();
                            }}
                          >
                            Change Password
                          </Button>
                        </div>
                      </Form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
}
