import React, { useState, useEffect } from "react";
import Header from "../Header";
import ls from "local-storage";
import { useNavigate } from "react-router-dom";
import bnbicon from "../images/bnbicon.png";
import busdicon from "../images/busdicon.png";
import egoldicon from "../images/egoldicon.png";
import "../css/dashboard.css";
import { useNotiStore } from "../Store/store";
import {
  IoArrowBackCircleOutline,
  IoClipboardOutline,
  IoEyeOutline,
} from "react-icons/io5";

import dataVal from "../data/dataval.json";
import { InfoOutlineIcon } from "@chakra-ui/icons";

const EGOLD_ADDRESS = dataVal.egoldtokencontract;
const BUSD_ADDRESS = dataVal.busdtokencontract;
const TOKEN_ABI = dataVal.busdtokenabi;

const Web3 = require("web3");

export default function Profile() {
  const web3 = new Web3(process.env.REACT_APP_RPC);

  let history = useNavigate();
  const status = ls.get("twoFaStatus");

  const [busdBalance, setBusdBalance] = useState(0);
  const [egoldBalance, setEgoldBalance] = useState(0);
  const [copied, setCopied] = useState(false);
  const [userbnbBalance, setUserbnbbalance] = useState("0");

  const appDisconnect = useNotiStore((state) => state.appDisconnect);

  const tokencontractInstance = new web3.eth.Contract(TOKEN_ABI, BUSD_ADDRESS);

  const egoldcontractInstance = new web3.eth.Contract(TOKEN_ABI, EGOLD_ADDRESS);

  useEffect(() => {
    getSummary();
  }, []);

  useEffect(() => {
    const token = localStorage.getItem("accessToken");
    if (token) {
      if (Date.now() - parseInt(localStorage.getItem("accessTime")) > 7200000) {
        localStorage.clear();
        appDisconnect();
      }
    }
    const interval = setInterval(() => {
      if (token) {
        if (
          Date.now() - parseInt(localStorage.getItem("accessTime")) >
          7200000
        ) {
          localStorage.clear();
          appDisconnect();
        }
      }
    }, 10000);

    return () => clearInterval(interval);
  }, []);

  const getSummary = async () => {
    var xbal = await web3.eth.getBalance(ls.get("acct"));
    setUserbnbbalance(noround(web3.utils.fromWei(xbal), 5));

    await tokencontractInstance.methods
      .balanceOf(ls.get("acct"))
      .call({}, function (e, res) {
        if (res) {
          setBusdBalance(noround(web3.utils.fromWei(res), 3));
        }
      });

    await egoldcontractInstance.methods
      .balanceOf(ls.get("acct"))
      .call({}, function (e, res) {
        setEgoldBalance(noround(web3.utils.fromWei(res), 5));
      });
  };
  //to not round values while showing balance
  function noround(val, x) {
    var float = parseFloat(val).toFixed(18);
    var num = float.toString();
    var n = num.slice(0, num.indexOf(".") + (x + 1));
    return n;
  }

  return (
    <>
      <Header />
      <div className="container">
        <section
          className="profilebody mb-5"
          style={{ backgroundColor: "rgb(37, 37, 47)", borderRadius: "14px" }}
        >
          <div className="container pb-5">
            <div className="row">
              <div className="col">
                <nav
                  aria-label="breadcrumb"
                  className="rounded-3 p-3 mb-4"
                  style={{
                    backgroundColor: "#1c1c23",
                    marginTop: "15px",
                    boxShadow: "0px 1px 8px 5px rgb(22 22 26 / 31%)",
                  }}
                >
                  <a
                    style={{ cursor: "pointer" }}
                    onClick={() => {
                      history("/");
                    }}
                  >
                    <IoArrowBackCircleOutline
                      color="white"
                      style={{ marginRight: 6, marginBottom: 2 }}
                    />
                  </a>
                  <ol
                    className="breadcrumb mb-0"
                    style={{ display: "inline-flex" }}
                  >
                    <li className="breadcrumb-item inactive">
                      <a
                        onClick={() => {
                          history("/");
                        }}
                      >
                        Home
                      </a>
                    </li>
                    <li className="breadcrumb-item active" aria-current="page">
                      Wallet
                    </li>
                  </ol>
                </nav>
              </div>
            </div>
            <div className="row">
              <div className="col-lg-4">
                <div className="card profilecard mb-4">
                  <div className="card-body text-center ">
                    {/* <img
                    src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-chat/ava3.webp"
                    alt="avatar"
                    className="rounded-circle img-fluid"
                    style={{ width: "150px" }}
                  /> */}
                    <p className="useradd mb-1">{ls.get("acct")}</p>
                    <div
                      className="d-flex justify-content-center mb-2"
                      style={{ display: "flex" }}
                    >
                      <span style={{ width: "50%" }}>
                        <button
                          type="button"
                          className="btn"
                          style={{
                            color: "white",
                            fontSize: "13px",
                            marginTop: "2px",
                          }}
                          onClick={async () => {
                            await navigator.clipboard.writeText(ls.get("acct"));
                            setCopied(true);
                            setTimeout(function () {
                              setCopied(false);
                            }, 2000);
                          }}
                        >
                          <IoClipboardOutline
                            color="white"
                            style={{ marginRight: 6, marginBottom: 2 }}
                          />
                          {copied ? "Copied" : "Copy"}
                        </button>
                      </span>
                      <span style={{ width: "50%" }}>
                        <a
                          type="button"
                          className="btn"
                          style={{ color: "white", fontSize: "13px" }}
                          href={"https://bscscan.com/address/" + ls.get("acct")}
                          target="_blank"
                        >
                          <IoEyeOutline
                            color="white"
                            style={{ marginRight: 6, marginBottom: 2 }}
                          />
                          View on Bscscan
                        </a>
                      </span>
                    </div>
                  </div>
                </div>
                <div className="card profilecard mb-4 mb-lg-0">
                  <div className="card-body p-0">
                    <ul className="list-group list-group-flush">
                      <a
                        data-toggle="collapse"
                        href="#collapseExample"
                        role="button"
                        aria-expanded="false"
                        aria-controls="collapseExample"
                      >
                        <li
                          className="list-group-item d-flex justify-content-between align-items-center p-3"
                          style={{ borderRadius: "15px 15px 0px 0px" }}
                        >
                          <p className="mb-0">
                            <i
                              class="bi bi-person"
                              style={{ fontSize: "20px", paddingRight: "10px" }}
                            ></i>{" "}
                            PROFILE
                          </p>
                        </li>
                      </a>
                      <div class="collapse" id="collapseExample">
                        <a
                          onClick={() => {
                            history("/profile");
                          }}
                        >
                          {" "}
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab ">
                            <p className="mb-0">Overview</p>
                          </li>
                        </a>

                        {status ? null : (
                          <a
                            onClick={() => {
                              history("/enableauthdash");
                            }}
                          >
                            <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab activetab">
                              <p className="mb-0">Enable 2FA</p>
                            </li>
                          </a>
                        )}

                        <a
                          onClick={() => {
                            history("/security");
                          }}
                        >
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab">
                            <p className="mb-0">Security</p>
                          </li>
                        </a>
                        <a
                          onClick={() => {
                            history("/changepw");
                          }}
                        >
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab">
                            <p className="mb-0">Change Password</p>
                          </li>
                        </a>
                      </div>

                      <li className="list-group-item d-flex justify-content-between align-items-center p-3 activetab">
                        <p className="mb-0">
                          <i
                            class="bi bi-wallet2"
                            style={{ fontSize: "20px", paddingRight: "10px" }}
                          ></i>{" "}
                          WALLET
                        </p>
                      </li>

                      <a
                        onClick={() => {
                          history("/transactions");
                        }}
                      >
                        <li
                          className="list-group-item d-flex justify-content-between align-items-center p-3"
                          style={{ borderRadius: "0px 0px 15px 15px" }}
                        >
                          <p className="mb-0">
                            {" "}
                            <i
                              class="bi bi-clock-history"
                              style={{ fontSize: "20px", paddingRight: "10px" }}
                            ></i>{" "}
                            TRANSACTION HISTORY
                          </p>
                        </li>
                      </a>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="col-lg-8">
                <div className="card profilecard mb-4">
                  <div className="card-body">
                    <div className="row">
                      <div className="col-sm-3">
                        <p
                          className="mb-0"
                          style={{ fontWeight: "900", fontSize: "22px" }}
                        >
                          <img
                            src={bnbicon}
                            className="img-fluid"
                            style={{
                              height: "25px",
                              paddingRight: "10px",
                              paddingBottom: "4px",
                            }}
                          />
                          BNB
                        </p>
                      </div>
                      <div className="col-sm-5 balmobpad">
                        <p
                          style={{
                            marginBottom: "0px",
                            fontSize: "20px",
                            fontWeight: "900",
                          }}
                        >
                          <span
                            className="text-muted mb-0"
                            style={{ fontSize: "15px" }}
                          >
                            Balance:
                          </span>{" "}
                          {userbnbBalance} BNB
                        </p>
                      </div>

                      <div className="col-sm-4 walletbuttons">
                        <button
                          class="btn btn-primary"
                          style={{
                            backgroundColor: "#5c4080",
                            margin: "0px 1px",
                          }}
                          onClick={() => {
                            history("/deposit/bnb");
                          }}
                        >
                          Deposit
                        </button>
                        <button
                          class="btn withdraw-btn"
                          onClick={() => {
                            history("/withdraw/bnb");
                          }}
                        >
                          Withdraw
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className="wallethrline">
                    <hr style={{ color: "#7c7777b3", margin: "10px" }} />
                  </div>

                  <div className="card-body">
                    <div className="row">
                      <div className="col-sm-3">
                        <p
                          className="mb-0"
                          style={{ fontWeight: "900", fontSize: "22px" }}
                        >
                          <img
                            src={busdicon}
                            className="img-fluid"
                            style={{
                              height: "25px",
                              paddingRight: "10px",
                              paddingBottom: "4px",
                            }}
                          />
                          BUSD
                        </p>
                      </div>
                      <div className="col-sm-5 balmobpad">
                        <p
                          style={{
                            marginBottom: "0px",
                            fontSize: "20px",
                            fontWeight: "900",
                          }}
                        >
                          <span
                            className="text-muted mb-0"
                            style={{ fontSize: "15px" }}
                          >
                            Balance:
                          </span>{" "}
                          {busdBalance} BUSD
                        </p>
                      </div>

                      <div className="col-sm-4 walletbuttons">
                        <button
                          class="btn btn-primary"
                          style={{
                            backgroundColor: "#5c4080",
                            margin: "0px 1px",
                          }}
                          onClick={() => {
                            history("/deposit/busd");
                          }}
                        >
                          Deposit
                        </button>
                        <button
                          class="btn withdraw-btn"
                          onClick={() => {
                            history("/withdraw/busd");
                          }}
                        >
                          Withdraw
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className="wallethrline">
                    <hr style={{ color: "#7c7777b3", margin: "10px" }} />
                  </div>

                  <div className="card-body">
                    <div className="row">
                      <div className="col-sm-3">
                        <p
                          className="mb-0"
                          style={{ fontWeight: "900", fontSize: "22px" }}
                        >
                          <img
                            src={egoldicon}
                            className="img-fluid"
                            style={{
                              height: "27px",
                              paddingRight: "10px",
                              paddingBottom: "4px",
                            }}
                          />
                          EGOLD
                        </p>
                      </div>

                      <div className="col-sm-5 balmobpad">
                        <p
                          style={{
                            marginBottom: "0px",
                            fontSize: "20px",
                            fontWeight: "900",
                          }}
                        >
                          <span
                            className="text-muted mb-0"
                            style={{ fontSize: "15px" }}
                          >
                            Balance:
                          </span>{" "}
                          {egoldBalance} EGOLD
                        </p>
                      </div>
                      <div className="col-sm-4 walletbuttons">
                        <button
                          class="btn btn-primary"
                          style={{
                            backgroundColor: "#5c4080",
                            margin: "0px 1px",
                          }}
                          onClick={() => {
                            history("/deposit/egold");
                          }}
                        >
                          Deposit
                        </button>
                        <button
                          class="btn withdraw-btn"
                          onClick={() => {
                            history("/withdraw/egold");
                          }}
                        >
                          Withdraw
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
}
