import { create } from "zustand";

const useStore = create((set) => ({
  notifications: {},
  toggle: false,
  connected: false,
  addNotifications: (notis) =>
    set((state) => ({
      notifications: notis,
    })),
  initiateNoti: () => set((state) => ({ toggle: !state.toggle })),
  appConnect: () => set((state) => ({ connected: true })),
  appDisconnect: () => set((state) => ({ connected: false })),
}));

export const useNotiStore = useStore;
