import React, { useEffect, useState } from "react";
import Header from "../Header";
import ls from "local-storage";
import { useNavigate } from "react-router-dom";
import { transactionHistory } from "../../api/apiService";
import "../css/dashboard.css";
import Loader from "../Loader";
import { useNotiStore } from "../Store/store";
import {
  IoArrowBackCircleOutline,
  IoClipboardOutline,
  IoEyeOutline,
} from "react-icons/io5";
import { TiTick } from "react-icons/ti";
import { ImCross } from "react-icons/im";

export default function Profile() {
  let history = useNavigate();
  const status = ls.get("twoFaStatus");

  const [tableData, setTabledata] = useState({ data: [], status: false });
  const [currentPage, setcurrentPage] = useState(0);
  const [totalPagecount, settotalPagecount] = useState(0);
  const [showloader, setShowLoader] = useState(false);
  const appDisconnect = useNotiStore((state) => state.appDisconnect);
  const [copied, setCopied] = useState(false);
  const getData = (page, fetch) => {
    setShowLoader(true);
    setTabledata({ status: false });
    if (Number(page) > 0) {
      if (fetch == 0) {
        //Previous Page
        const token = ls.get("accessToken");
        const headerhistory = {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization: "Bearer " + String(token),
          },
        };
        transactionHistory(page, headerhistory).then((json) => {
          console.log("JSON HERE", json.data.data);
          let pagecounthere = Number(json.data.total_transactions / 10);
          settotalPagecount(Math.round(pagecounthere)); //Total pagecount
          setcurrentPage(currentPage - 1); //Current page
          setTabledata({ data: json.data.data, status: true });
          setShowLoader(false);
        });
      } else if (fetch == 1) {
        //Fetch Next page
        const token = ls.get("accessToken");
        const headerhistory = {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization: "Bearer " + String(token),
          },
        };
        transactionHistory(page, headerhistory).then((json) => {
          console.log("JSON HERE", json.data.data);
          let pagecounthere = Number(json.data.total_transactions / 10);
          settotalPagecount(Math.round(pagecounthere)); //Total pagecount
          setcurrentPage(currentPage + 1); //Current page
          setTabledata({ data: json.data.data, status: true });
          setShowLoader(false);
        });
      }
    }
  };

  useEffect(() => {
    getData("1", 1);
    const token = localStorage.getItem("accessToken");
    if (token) {
      if (Date.now() - parseInt(localStorage.getItem("accessTime")) > 7200000) {
        localStorage.clear();
        appDisconnect();
      }
    }
    const interval = setInterval(() => {
      if (token) {
        if (
          Date.now() - parseInt(localStorage.getItem("accessTime")) >
          7200000
        ) {
          localStorage.clear();
          appDisconnect();
        }
      }
    }, 10000);

    return () => clearInterval(interval);
  }, []);



  // to get proper task name
  function checkNotiTask(task) {
    if (task == "wallet_creation") {
      return "Wallet Creation";
    } else if (task == "claim") {
      return "Claim";
    } else if (task == "purcahse_miner") {
      return "Purchase Miner";
    } else if (task == "stake_miner") {
      return "Stake Miner";
    } else if (task == "unstake_miner") {
      return "Unstake Miner";
    } else if (task == "transfer_miner") {
      return "Transfer Miner";
    } else if (task == "delete_miner") {
      return "Delete Miner";
    } else if (task == "withdraw_bnb") {
      return "BNB Withdraw";
    } else if (task == "withdraw_token") {
      return "Token Withdraw";
    } else if (task == "gift_miner") {
      return "Giveaway";
    }
    else if (task == "Deposit") {
      return "Deposit";
    } else if (task == "addLiquidity") {
      return "Add Liquidity";
    }
    else if (task == "swapTokenforToken") {
      return "Swap Token";
    } else if (task == "removeLiquidity") {
      return "Remove Liquidity";
    }
    else if (task == "depositLPfarm") {
      return "LP Farm Deposit";
    }
    else if (task == "withdrawLPyeild") {
      return "LP Farm Withdraw";
    }
    else if (task == "claimFreeminer") {
      return "Claim Free Miner";
    }


  }

  function formatDate(date) {
    var currentime = new Date();
    let createdTasktime = new Date(date);
    const convertedDate = new Date(
      createdTasktime.getTime() -
      createdTasktime.getTimezoneOffset() * 60 * 1000
    );
    var diff = currentime - convertedDate;
    var msec = diff;
    var hh = Math.floor(msec / 1000 / 60 / 60);
    msec -= hh * 1000 * 60 * 60;
    var mm = Math.floor(msec / 1000 / 60);
    msec -= mm * 1000 * 60;
    var ss = Math.floor(msec / 1000);
    msec -= ss * 1000;
    if (hh > 24) {
      var convertedtime = SplitTime(hh + "." + mm);
      console.log("CONVERTEDTIME", convertedtime);
      return (
        String(convertedtime.Days) +
        " Day " +
        String(convertedtime.Hours) +
        " Hours ago"
      );
    } else {
      return hh + " hour " + mm + " mins ago";
    }
  }
  function SplitTime(numberOfHours) {
    //convert time to days
    var Days = Math.floor(numberOfHours / 24);
    var Remainder = numberOfHours % 24;
    var Hours = Math.floor(Remainder);
    var Minutes = Math.floor(60 * (Remainder - Hours));
    return { Days: Days, Hours: Hours, Minutes: Minutes };
  }
  function checkItemStatus(items) {
    if (items.status == "success") {
      return <TiTick color="green" size={18} />;
    } else {
      return <ImCross color="#AF4346" size={12} />;
    }
  }
  function checkToken(token) {
    if (token == "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56") {
      return (" BUSD")
    } else {
      return (" EGOLD")
    }
  }
  function checkAmountCol(items) {
    console.log("ITEMSS COL", items);
    if (items.task == "Deposit") {
      return items.params.value + " " + items.params.currency;
    } else if (items.task == "withdraw_bnb") {
      return items.params.value + " " + "BNB";
    } else if (items.task == "withdraw_token") {
      return items.params.value + " " + items.params.token;
    } else if (items.task == "depositLPfarm") {
      return items.params.amount + " LPT";
    } else if (items.task == "withdrawLPyeild") {
      return items.params.amount + " LPT";
    } else if (items.task == "addLiquidity") {
      return items.params.fromAmount !== null
        ? parseFloat(items.params.tokenAamnt).toFixed(2) +
        checkToken(items.params.tokenA) +
        " + " +
        parseFloat(items.params.tokenBamnt).toFixed(2) +
        checkToken(items.params.tokenB)
        : parseFloat(0).toFixed(2) +
        checkToken(items.params.tokenA) +
        " + " +
        parseFloat(items.params.tokenBamnt).toFixed(2) +
        checkToken(items.params.tokenB);
    } else if (items.task == "swapTokenforToken") {
      return items.params.fromAmount !== null
        ? parseFloat(items.params.fromAmount).toFixed(2) +
        checkToken(items.params.fromToken) +
        " To " +
        parseFloat(items.params.toAmount).toFixed(2) +
        checkToken(items.params.toToken)
        : parseFloat(0).toFixed(2) +
        checkToken(items.params.fromToken) +
        " To " +
        parseFloat(items.params.toAmount).toFixed(2) +
        checkToken(items.params.toToken);
    } else {
      return "....";
    }
  }

  return (
    <>
      <Header />
      <div className="container">
        <section
          className="profilebody mb-5"
          style={{ backgroundColor: "rgb(37, 37, 47)", borderRadius: "14px" }}
        >
          <div className="container pb-5">
            <div className="row">
              <div className="col">
                <nav
                  aria-label="breadcrumb"
                  className="rounded-3 p-3 mb-4"
                  style={{
                    backgroundColor: "#1c1c23",
                    marginTop: "15px",
                    boxShadow: "0px 1px 8px 5px rgb(22 22 26 / 31%)",
                  }}
                >
                  <a
                    style={{ cursor: "pointer" }}
                    onClick={() => {
                      history("/");
                    }}
                  >
                    <IoArrowBackCircleOutline
                      color="white"
                      style={{ marginRight: 6, marginBottom: 2 }}
                    />
                  </a>
                  <ol
                    className="breadcrumb mb-0"
                    style={{ display: "inline-flex" }}
                  >
                    <li className="breadcrumb-item inactive">
                      <a
                        onClick={() => {
                          history("/");
                        }}
                      >
                        Home
                      </a>
                    </li>
                    <li className="breadcrumb-item active" aria-current="page">
                      Transactions
                    </li>
                  </ol>
                </nav>
              </div>
            </div>
            <div className="row">
              <div className="col-lg-4">
                <div className="card profilecard mb-4">
                  <div className="card-body text-center">
                    {/* <img
                    src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-chat/ava3.webp"
                    alt="avatar"
                    className="rounded-circle img-fluid"
                    style={{ width: "150px" }}
                  /> */}
                    <p className="useradd mb-1">{ls.get("acct")}</p>
                    <div
                      className="d-flex justify-content-center mb-2"
                      style={{ display: "flex" }}
                    >
                      <span style={{ width: "50%" }}>
                        <button
                          type="button"
                          className="btn"
                          style={{
                            color: "white",
                            fontSize: "13px",
                            marginTop: "2px",
                          }}
                          onClick={async () => {
                            await navigator.clipboard.writeText(ls.get("acct"));
                            setCopied(true);
                            setTimeout(function () {
                              setCopied(false);
                            }, 2000);
                          }}
                        >
                          <IoClipboardOutline
                            color="white"
                            style={{ marginRight: 6, marginBottom: 2 }}
                          />
                          {copied ? "Copied" : "Copy"}
                        </button>
                      </span>
                      <span style={{ width: "50%" }}>
                        <a
                          type="button"
                          className="btn"
                          style={{ color: "white", fontSize: "13px" }}
                          href={"https://bscscan.com/address/" + ls.get("acct")}
                          target="_blank"
                        >
                          <IoEyeOutline
                            color="white"
                            style={{ marginRight: 6, marginBottom: 2 }}
                          />
                          View on Bscscan
                        </a>
                      </span>
                    </div>
                  </div>
                </div>
                <div className="card profilecard mb-4 mb-lg-0">
                  <div className="card-body p-0">
                    <ul
                      className="list-group list-group-flush"
                      style={{ borderRadius: "15px" }}
                    >
                      <a
                        data-toggle="collapse"
                        href="#collapseProfile"
                        role="button"
                        aria-expanded="true"
                        aria-controls="collapseProfile"
                      >
                        <li
                          className="list-group-item d-flex justify-content-between align-items-center p-3"
                          style={{ borderRadius: "15px 15px 0px 0px" }}
                        >
                          <p className="mb-0">
                            <i
                              class="bi bi-person"
                              style={{ fontSize: "20px", paddingRight: "10px" }}
                            ></i>{" "}
                            PROFILE
                          </p>
                        </li>
                      </a>
                      <div class="collapse" id="collapseProfile">
                        <a
                          onClick={() => {
                            history("/profile");
                          }}
                        >
                          {" "}
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab">
                            <p className="mb-0">Overview</p>
                          </li>
                        </a>

                        {status ? null : (
                          <a
                            onClick={() => {
                              history("/enableauthdash");
                            }}
                          >
                            <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab activetab">
                              <p className="mb-0">Enable 2FA</p>
                            </li>
                          </a>
                        )}

                        <a
                          onClick={() => {
                            history("/security");
                          }}
                        >
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab">
                            <p className="mb-0">Security</p>
                          </li>
                        </a>
                        <a
                          onClick={() => {
                            history("/changepw");
                          }}
                        >
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab">
                            <p className="mb-0">Change Password</p>
                          </li>
                        </a>
                      </div>
                      <a
                        onClick={() => {
                          history("/wallet");
                        }}
                      >
                        <li className="list-group-item d-flex justify-content-between align-items-center p-3">
                          <p className="mb-0">
                            <i
                              class="bi bi-wallet2"
                              style={{ fontSize: "20px", paddingRight: "10px" }}
                            ></i>{" "}
                            WALLET
                          </p>
                        </li>
                      </a>
                      <li className="list-group-item d-flex justify-content-between align-items-center p-3 activetab">
                        <p className="mb-0">
                          {" "}
                          <i
                            class="bi bi-clock-history"
                            style={{ fontSize: "20px", paddingRight: "10px" }}
                          ></i>{" "}
                          TRANSACTION HISTORY
                        </p>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="col-lg-8">
                <div
                  className="card profilecard mb-4 tablecard"
                  style={{ overflowY: "auto" }}
                >
                  <div className="card-body" style={{ padding: "2px" }}>
                    <table class="table table-sm table-dark">
                      <thead>
                        <tr>
                          <th scope="col">#</th>
                          <th scope="col">Type</th>
                          <th scope="col">Amount</th>
                          {/* <th scope="col">Status</th> */}
                          <th scope="col">Tx Hash</th>
                          <th scope="col">Time</th>
                          <th scope="col"> </th>
                        </tr>
                      </thead>

                      <tbody>
                        {showloader ? (
                          <Loader />
                        ) : tableData.status ? (
                          tableData.data.length > 0 ? (
                            tableData.data.map((item, index) => (
                              <>
                                <tr
                                  style={{ cursor: "pointer" }}
                                  onClick={() =>
                                    history("/transactions/" + item._id)
                                  }
                                >
                                  <td>{index + 1}</td>
                                  <td>{checkNotiTask(item.task)}</td>
                                  <td>{checkAmountCol(item)}</td>
                                  {/* <td
                                  style={
                                    item.status == "success"
                                      ? { color: "green" }
                                      : item.status == "failed"
                                        ? { color: "red" }
                                        : { color: "orange" }
                                  }
                                >
                                  {String(item.status).toUpperCase()}
                                </td> */}
                                  <td>
                                    {" "}
                                    {item.status == "success"
                                      ? item.task == "wallet_creation"
                                        ? "...."
                                        : item.result.substring(0, 5) +
                                        "..." +
                                        item.result.substring(
                                          item.result.length - 5
                                        )
                                      : "...."}
                                  </td>
                                  <td>{formatDate(item.created_time)}</td>
                                  <td>{checkItemStatus(item)}</td>
                                </tr>
                              </>
                            ))
                          ) : (
                            console.log("not available")
                          )
                        ) : (
                          console.log("not available")
                        )}
                      </tbody>
                    </table>
                  </div>
                </div>
                <div>
                  <span
                    style={{
                      display: "flex",
                      gap: "20px",
                      justifyContent: "right",
                    }}
                  >
                    <nav
                      aria-label="Page navigation example"
                      style={{
                        "margin-right": "10px",
                      }}
                    >
                      <ul class="pagination" style={{ fontSize: "11px" }}>
                        <li class="page-item">
                          <a
                            class="page-link"
                            href="#"
                            style={
                              currentPage == 1 ? { display: "none" } : null
                            }
                            onClick={() => {
                              getData(currentPage - 1, 0);
                            }}
                          >
                            Previous
                          </a>
                        </li>
                        <li class="page-item">
                          <a class="page-link" href="#">
                            Page {currentPage} of {totalPagecount}
                          </a>
                        </li>
                        <li
                          class="page-item"
                          style={
                            totalPagecount == currentPage
                              ? { display: "none" }
                              : null
                          }
                        >
                          <a
                            class="page-link"
                            href="#"
                            onClick={() => {
                              getData(currentPage + 1, 1);
                            }}
                          >
                            Next
                          </a>
                        </li>
                      </ul>
                    </nav>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
}
