import React, { useState, useEffect } from "react";
import ls from "local-storage";
import { useNavigate, useParams } from "react-router-dom";
import "../css/dashboard.css";
import {
  getBNBhistory,
  getBUSDhistory,
  getEGOLDhistory,
} from "../../api/apiService";
import dataVal from "../data/dataval.json";
import Loader from "../Loader";
import { BsCheckLg, BsXLg } from "react-icons/bs";
import { RiHistoryLine } from "react-icons/ri";

const EGOLD_ADDRESS = dataVal.egoldtokencontract;
const BUSD_ADDRESS = dataVal.busdtokencontract;
const TOKEN_ABI = dataVal.busdtokenabi;

const Web3 = require("web3");

export default function HistoryParticular(props) {
  let params = useParams();
  console.log("PROPS", props);
  const web3 = new Web3(process.env.REACT_APP_RPC);

  const tokenselected = props.state;
  // const tokenselected = params.type
  let history = useNavigate();
  const status = ls.get("twoFaStatus");
  const [tableData, setTabledata] = useState({ data: [], status: false });
  const [tooltipOpen, settooltipOpen] = useState(false);
  const [busdBalance, setBusdBalance] = useState(0);
  const [egoldBalance, setEgoldBalance] = useState(0);
  const [copied, setCopied] = useState(false);
  const [userbnbBalance, setUserbnbbalance] = useState("0");

  const tokencontractInstance = new web3.eth.Contract(TOKEN_ABI, BUSD_ADDRESS);

  const egoldcontractInstance = new web3.eth.Contract(TOKEN_ABI, EGOLD_ADDRESS);

  useEffect(() => {
    getSummary();
    fetchHistory();
  }, []);
  useEffect(() => {
    const token = localStorage.getItem("accessToken");
    if (token) {
      if (Date.now() - parseInt(localStorage.getItem("accessTime")) > 7200000) {
        localStorage.clear();
        history("/login");
      }
    }
    const interval = setInterval(() => {
      if (token) {
        if (
          Date.now() - parseInt(localStorage.getItem("accessTime")) >
          7200000
        ) {
          localStorage.clear();
          history("/login");
        }
      }
    }, 10000);

    return () => clearInterval(interval);
  }, []);

  const getSummary = async () => {
    var xbal = await web3.eth.getBalance(ls.get("acct"));
    setUserbnbbalance(noround(web3.utils.fromWei(xbal), 5));

    await tokencontractInstance.methods
      .balanceOf(ls.get("acct"))
      .call({}, function (e, res) {
        if (res) {
          setBusdBalance(noround(web3.utils.fromWei(res), 4));
        }
      });

    await egoldcontractInstance.methods
      .balanceOf(ls.get("acct"))
      .call({}, function (e, res) {
        setEgoldBalance(noround(web3.utils.fromWei(res), 5));
      });
  };

  //Get history transactions
  async function fetchHistory() {
    const token = localStorage.getItem("accessToken");
    // const token = "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJzdWIiOiJlbHRvbjEwdGhvbWFzQGdtYWlsLmNvbSIsImlhdCI6MTY3MTM0Mjg1MywibmJmIjoxNjcxMzQyODUzLCJqdGkiOiJlOGMyZTBkMi1hMTJiLTQ3NmQtYTFjNy1mMjU0ZjQ2M2VmMDQiLCJleHAiOjE2NzEzNTAwNTMsInR5cGUiOiJhY2Nlc3MiLCJmcmVzaCI6ZmFsc2V9.jLNSpMOk00xI45S1S94nBI0akbzTzSSJ6hsMMx_KS7w"
    var headers = {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: "Bearer " + String(token),
      },
    };
    if (tokenselected == "BNB") {
      getBNBhistory(headers).then((json) => {
        console.log("TABLE DATA", json.data);
        setTabledata({ data: json.data, status: true });
      });
    } else if (tokenselected == "BUSD") {
      getBUSDhistory(headers).then((json) => {
        setTabledata({ data: json.data, status: true });
      });
    } else if (tokenselected == "EGOLD") {
      getEGOLDhistory(headers).then((json) => {
        setTabledata({ data: json.data, status: true });
      });
    }
  }
  //to not round values while showing balance
  function noround(val, x) {
    var float = parseFloat(val).toFixed(18);
    var num = float.toString();
    var n = num.slice(0, num.indexOf(".") + (x + 1));
    return n;
  }
  function toggle() {
    settooltipOpen(!tooltipOpen);
  }
  function checkNotiTask(task) {
    if (task == "wallet_creation") {
      return "Wallet Creation";
    } else if (task == "claim") {
      return "Claim";
    } else if (task == "purcahse_miner") {
      return "Purchase Miner";
    } else if (task == "stake_miner") {
      return "Stake Miner";
    } else if (task == "unstake_miner") {
      return "Unstake Miner";
    } else if (task == "transfer_miner") {
      return "Transfer Miner";
    } else if (task == "delete_miner") {
      return "Delete Miner";
    } else if (task == "withdraw_bnb") {
      return "BNB Withdraw";
    } else if (task == "withdraw_token") {
      return "Token Withdraw";
    } else if (task == "gift_miner") {
      return "Giveaway";
    } else if (task == "Deposit") {
      return "Deposit";
    } else if (task == "addLiquidity") {
      return "Add Liquidity";
    } else if (task == "swapTokenforToken") {
      return "Swap Token";
    } else if (task == "removeLiquidity") {
      return "Remove Liquidity";
    } else if (task == "depositLPfarm") {
      return "LP Farm Deposit";
    } else if (task == "withdrawLPyeild") {
      return "LP Farm Withdraw";
    }
  }
  function formatDate(date) {
    var currentime = new Date();
    let createdTasktime = new Date(date);
    const convertedDate = new Date(
      createdTasktime.getTime() -
        createdTasktime.getTimezoneOffset() * 60 * 1000
    );
    var diff = currentime - convertedDate;
    var msec = diff;
    var hh = Math.floor(msec / 1000 / 60 / 60);
    msec -= hh * 1000 * 60 * 60;
    var mm = Math.floor(msec / 1000 / 60);
    msec -= mm * 1000 * 60;
    var ss = Math.floor(msec / 1000);
    msec -= ss * 1000;
    if (hh > 24) {
      var convertedtime = SplitTime(hh + "." + mm);
      return (
        String(convertedtime.Days) +
        " Day " +
        String(convertedtime.Hours) +
        " Hours ago"
      );
    } else {
      return hh + " hour " + mm + " mins ago";
    }
  }
  function SplitTime(numberOfHours) {
    //convert time to days
    var Days = Math.floor(numberOfHours / 24);
    var Remainder = numberOfHours % 24;
    var Hours = Math.floor(Remainder);
    var Minutes = Math.floor(60 * (Remainder - Hours));
    return { Days: Days, Hours: Hours, Minutes: Minutes };
  }
  function checkIcon(status) {
    if (status == "success") {
      return <BsCheckLg color={"green"} size={14} />;
    } else {
      return <BsXLg color="#FF0E00" size={14} />;
    }
  }
  function onViewTransaction(data) {
    if (data.status == "success") {
      // similar behavior as clicking on a link
      window.open("https://bscscan.com/tx/" + data.result, "_blank");
    } else {
      history("/transactions/" + data._id);
    }
  }
  return (
    <>
      <div className="card profilecard mb-4">
        <div style={{ display: "flex", marginLeft: 5 }}>
          <RiHistoryLine color="grey" size={23} />
          <p
            style={{
              marginLeft: "4px",
              color: "#a0a0a0",
            }}
          >
            {tokenselected} History
          </p>
        </div>

        {tableData.status ? (
          tableData.data.length >= 1 ? (
            <div style={{ overflowY: "scroll" }}>
              <table class="table table-sm table-dark">
                <thead>
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">Task</th>
                    <th scope="col">Amount</th>
                    {/* <th scope="col">Status</th> */}
                    <th scope="col">Tx Hash</th>
                    <th scope="col">Time</th>
                    <th scope="col">Status </th>
                  </tr>
                </thead>

                <tbody className="PhistoryTable">
                  {tableData.data.map((item, index) => (
                    <tr
                      style={{ cursor: "pointer" }}
                      onClick={() => {
                        onViewTransaction(item);
                      }}
                    >
                      {console.log("ITEMS", item)}
                      <td>{index + 1}</td>
                      <td> {checkNotiTask(item.task)}</td>
                      <td> {noround(item.params.value, 3)}</td>
                      <td>
                        {item.status == "success"
                          ? item.result.substring(0, 5) +
                            "..." +
                            item.result.substring(item.result.length - 5)
                          : "...."}
                      </td>
                      <td> {formatDate(item.created_time)}</td>
                      <td>{checkIcon(item.status)}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <div
              style={{
                justifyContent: "center",
                alignItems: "center",
                display: "flex",
              }}
            >
              <RiHistoryLine color="grey" size={30} />
              <p
                style={{
                  "margin-top": "17px",
                  "margin-left": "10px",
                  color: "grey",
                }}
              >
                Your Transactions appear here
              </p>
            </div>
          )
        ) : (
          <Loader />
        )}
      </div>
    </>
  );
}
