  const [dailystate, setDailystate] = useState({ data: null, loading: true });
  const [summary, setSummary] = useState({ data: null, loading: true });
  const [gcount1, setGcount1] = useState({ data: null, loading: true });
  const [gcount2, setGcount2] = useState({ data: null, loading: true });
  const [gcount3, setGcount3] = useState({ data: null, loading: true });
  const [gcount4, setGcount4] = useState({ data: null, loading: true });
  const [gmcount, setGmcount] = useState({ data: null, loading: true });
