    const urlMiningStats = "https://egold-miner.herokuapp.com/miningStats";
    const urlsummary = "https://egold-marketdata.herokuapp.com/v1/summary/";
    const url24 =
      "https://egold-marketdata.herokuapp.com/v1/summary/get24MarketStat";
    const url = "https://egold-treasury.tagdev.info/v1/summary";
    const g1count =
      "https://egold-treasury-api.herokuapp.com/v1/summary/countMiners/1";
    const g2count =
      "https://egold-treasury-api.herokuapp.com/v1/summary/countMiners/2";
    const g3count =
      "https://egold-treasury-api.herokuapp.com/v1/summary/countMiners/3";
    const g4count =
      "https://egold-treasury-api.herokuapp.com/v1/summary/countMiners/4";
    const gmcount = "https://auth.egoldfarm.com/getgiftminercount";

    //fetch 24 hour market data
    fetch(urltd)
      .then(function (response) {
        return response.json();
      })
      .then(function (newData) {
        setTradestate({ data: newData, loading: false });
      })
      .catch(function (error) {
        console.log(error);
      });
    //MiningStats
    fetch(urlMiningStats)
      .then(function (responseStats) {
        return responseStats.json();
      })
      .then(function (newStats) {
        setMiningStats({ data: newStats, loading: false });
      })
      .catch(function (error) {
        console.log("Requestfailed", error);
      });

    //fetch 24 hour market data
    fetch(urlsummary)
      .then(function (response) {
        return response.json();
      })
      .then(function (newData) {
        setEGsummary({ data: newData, loading: false });
      })
      .catch(function (error) {
        console.log(error);
      });

    //fetch 24 hour market data
    fetch(url24)
