import React, { useEffect, useState } from "react";
import Header from "../Header";
import ls from "local-storage";
import { useNavigate } from "react-router-dom";
import { useNotiStore } from "../Store/store";
import {
  IoArrowBackCircleOutline,
  IoClipboardOutline,
  IoEyeOutline,
} from "react-icons/io5";

const NFT_ADDRESS = process.env.REACT_APP_NFT_ADDR;
const NFT_ABI = require("../data/nftAbi.json");
const Web3 = require("web3");

export default function Profile() {
  let history = useNavigate();
  const [copied, setCopied] = useState(false);
  const [stakedCount, setStakedCount] = useState(0);
  const [minerOwned, setMinersOwned] = useState("...");
  const appDisconnect = useNotiStore((state) => state.appDisconnect);
  const months = {
    1: "Jan",
    2: "Feb",
    3: "Mar",
    4: "Apr",
    5: "May",
    6: "Jun",
    7: "Jul",
    8: "Aug",
    9: "Sep",
    10: "Oct",
    11: "Nov",
    12: "Dec",
  };
  useEffect(() => {
    const token = localStorage.getItem("accessToken");
    if (token) {
      if (Date.now() - parseInt(localStorage.getItem("accessTime")) > 7200000) {
        localStorage.clear();
        appDisconnect();
      }
    }
    const interval = setInterval(() => {
      if (token) {
        if (
          Date.now() - parseInt(localStorage.getItem("accessTime")) >
          7200000
        ) {
          localStorage.clear();
          appDisconnect();
        }
      }
    }, 10000);

    return () => clearInterval(interval);
  }, []);

  const status = ls.get("twoFaStatus");
  const web3 = new Web3(process.env.REACT_APP_RPC);
  const nftInstance = new web3.eth.Contract(NFT_ABI, NFT_ADDRESS);
  const [minestatData, setMineStatData] = useState({
    data: null,
    loading: true,
  });
  const [walletcreatedDate, setWalletcreatedDate] = useState("");
  const [tradestatData, setTradeStatData] = useState({
    data: null,
    loading: true,
  });

  const getSummary = async () => {
    //Get accountcreatedTime
    let createdtime = new Date(localStorage.getItem("createdAt"));

    let convertedDate = new Date(
      createdtime.getTime() - createdtime.getTimezoneOffset() * 60 * 1000
    );
    var month = convertedDate.getUTCMonth() + 1; //months from 1-12
    var day = convertedDate.getUTCDate();
    var year = convertedDate.getUTCFullYear();
    var ddata = day + " " + months[month] + " " + year;
    console.log(" DDAT", ddata);
    setWalletcreatedDate(ddata);

    //Get Number of miners
    await nftInstance.methods
      .balanceOf(ls.get("acct"))
      .call({}, function (e, res) {
        console.log("Miners Owned", res);
        setMinersOwned(res);
      });
    //Get number of staked miners
    const url2 = "https://egold-miner.tagdev.info/miner/" + ls.get("acct");
    fetch(url2)
      .then(function (response) {
        return response.json();
      })
      .then(function (newData) {
        if (Array.isArray(newData)) {
          console.log("Miners Staked", newData.length);
          setStakedCount(newData.length);
        }
      });
    //Get mining data
    const minestaturl =
      "https://egold-miner.tagdev.info/mininginfo/" + ls.get("acct");

    fetch(minestaturl)
      .then(function (response) {
        return response.json();
      })
      .then(function (newData) {
        setMineStatData({ data: newData, loading: false });
      })
      .catch(function (error) {
        console.log("Requestfailed", error);
      });
    //Get trade data
    const tradestaturl =
      "https://egold-marketdata.herokuapp.com/v1/summary/getTradeSummary/" +
      ls.get("acct");

    fetch(tradestaturl)
      .then(function (response) {
        return response.json();
      })
      .then(function (newData) {
        setTradeStatData({ data: newData, loading: false });
      })
      .catch(function (error) {
        console.log("Requestfailed", error);
      });
  };

  useEffect(() => {
    getSummary();
  }, []);
  return (
    <>
      <Header />
      <div className="container">
        <section
          className="profilebody mb-5"
          style={{ backgroundColor: "rgb(37, 37, 47)", borderRadius: "14px" }}
        >
          <div className="container pb-5">
            <div className="row">
              <div className="col">
                <nav
                  aria-label="breadcrumb"
                  className="rounded-3 p-3 mb-4"
                  style={{
                    backgroundColor: "#1c1c23",
                    marginTop: "15px",
                    boxShadow: "0px 1px 8px 5px rgb(22 22 26 / 31%)",
                  }}
                >
                  <a
                    style={{ cursor: "pointer" }}
                    onClick={() => {
                      history("/");
                    }}
                  >
                    <IoArrowBackCircleOutline
                      color="white"
                      style={{ marginRight: 6, marginBottom: 2 }}
                    />
                  </a>
                  <ol
                    className="breadcrumb mb-0"
                    style={{ display: "inline-flex" }}
                  >
                    <li className="breadcrumb-item inactive">
                      <a
                        onClick={() => {
                          history("/");
                        }}
                      >
                        Home
                      </a>
                    </li>
                    <li className="breadcrumb-item active" aria-current="page">
                      Profile
                    </li>
                  </ol>
                </nav>
              </div>
            </div>
            <div className="row">
              <div className="col-lg-4">
                <div className="card profilecard mb-4">
                  <div className="card-body text-center ">
                    <p className="useradd mb-3">{ls.get("acct")}</p>
                    <div
                      className="d-flex justify-content-center mb-2"
                      style={{ display: "flex" }}
                    >
                      <span style={{ width: "50%" }}>
                        <button
                          type="button"
                          className="btn"
                          style={{
                            color: "white",
                            fontSize: "13px",
                            marginTop: "2px",
                          }}
                          onClick={async () => {
                            await navigator.clipboard.writeText(ls.get("acct"));
                            setCopied(true);
                            setTimeout(function () {
                              setCopied(false);
                            }, 2000);
                          }}
                        >
                          <IoClipboardOutline
                            color="white"
                            style={{ marginRight: 6, marginBottom: 2 }}
                          />
                          {copied ? "Copied" : "Copy"}
                        </button>
                      </span>
                      <span style={{ width: "50%" }}>
                        <a
                          type="button"
                          className="btn"
                          style={{ color: "white", fontSize: "13px" }}
                          href={"https://bscscan.com/address/" + ls.get("acct")}
                          target="_blank"
                        >
                          <IoEyeOutline
                            color="white"
                            style={{ marginRight: 6, marginBottom: 2 }}
                          />
                          View on Bscscan
                        </a>
                      </span>
                    </div>
                  </div>
                </div>
                <div className="card profilecard mb-4 mb-lg-0">
                  <div className="card-body p-0">
                    <ul className="list-group list-group-flush">
                      <a
                        data-toggle="collapse"
                        href="#collapseExample"
                        role="button"
                        aria-expanded="false"
                        aria-controls="collapseExample"
                      >
                        <li
                          className="list-group-item d-flex justify-content-between align-items-center p-3"
                          style={{ borderRadius: "15px 15px 0px 0px" }}
                        >
                          <p className="mb-0">
                            <i
                              class="bi bi-person"
                              style={{ fontSize: "20px", paddingRight: "10px" }}
                            ></i>{" "}
                            PROFILE
                          </p>
                        </li>
                      </a>
                      <div class="collapse show" id="collapseExample">
                        <a
                          onClick={() => {
                            history("/profile");
                          }}
                        >
                          {" "}
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab activetab">
                            <p className="mb-0">Overview</p>
                          </li>
                        </a>

                        {status ? null : (
                          <a
                            onClick={() => {
                              history("/enableauthdash");
                            }}
                          >
                            <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab ">
                              <p className="mb-0">Enable 2FA</p>
                            </li>
                          </a>
                        )}

                        <a
                          onClick={() => {
                            history("/security");
                          }}
                        >
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab">
                            <p className="mb-0">Security</p>
                          </li>
                        </a>
                        <a
                          onClick={() => {
                            history("/changepw");
                          }}
                        >
                          <li className="list-group-item d-flex justify-content-between align-items-center p-3 opentab">
                            <p className="mb-0">Change Password</p>
                          </li>
                        </a>
                      </div>

                      <a
                        onClick={() => {
                          history("/wallet");
                        }}
                      >
                        <li className="list-group-item d-flex justify-content-between align-items-center p-3">
                          <p className="mb-0">
                            <i
                              class="bi bi-wallet2"
                              style={{ fontSize: "20px", paddingRight: "10px" }}
                            ></i>{" "}
                            WALLET
                          </p>
                        </li>
                      </a>
                      <a
                        onClick={() => {
                          history("/transactions");
                        }}
                      >
                        <li
                          className="list-group-item d-flex justify-content-between align-items-center p-3"
                          style={{ borderRadius: "0px 0px 15px 15px" }}
                        >
                          <p className="mb-0">
                            {" "}
                            <i
                              class="bi bi-clock-history"
                              style={{ fontSize: "20px", paddingRight: "10px" }}
                            ></i>{" "}
                            TRANSACTION HISTORY
                          </p>
                        </li>
                      </a>
                    </ul>
                  </div>
                </div>
              </div>
              <div className="col-lg-8">
                <div className="card profilecard mb-4">
                  <div className="card-body">
                    <hr />
                    <div className="row" style={{ padding: "0px 20px" }}>
                      <div className="col-sm-4" style={{ padding: 0 }}>
                        <p className="mb-0">Email</p>
                      </div>
                      <div
                        className="col-sm-8"
                        style={{ padding: 0, fontSize: 14 }}
                      >
                        <p className="text-muted mb-0">{ls.get("email")}</p>
                      </div>
                    </div>
                    <hr />
                    {ls.get("createdAt") ? (
                      <>
                        <div className="row" style={{ padding: "0px 20px" }}>
                          <div className="col-sm-4" style={{ padding: 0 }}>
                            <p className="mb-0">Account Created On </p>
                          </div>
                          <div
                            className="col-sm-8"
                            style={{ padding: 0, fontSize: 14 }}
                          >
                            <p className="text-muted mb-0">
                              {walletcreatedDate}
                            </p>
                          </div>
                        </div>
                        <hr />
                      </>
                    ) : (
                      ""
                    )}
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-6">
                    <div className="card profilecard mb-4 pb-3 mb-md-0">
                      <div className="card-body tstatuscbody">
                        <p className="mb-4 headstatusr">Mining Status</p>
                        <p className="mb-1" style={{ fontSize: "0.77rem" }}>
                          Miners Owned :
                          <span className="tstatusspan">
                            {parseInt(minerOwned) + stakedCount}/25
                          </span>
                        </p>
                        <div
                          className="progress rounded"
                          style={{ height: "5px", marginTop: "10px" }}
                        >
                          <div
                            className="progress-bar"
                            role="progressbar"
                            style={{
                              width:
                                ((parseInt(minerOwned) + stakedCount) / 25) *
                                  100 +
                                "%",
                            }}
                            aria-valuenow={20}
                            aria-valuemin={0}
                            aria-valuemax={100}
                          />
                        </div>
                        <br />
                        <p
                          className="mt-4 mb-1"
                          style={{ fontSize: "0.77rem" }}
                        >
                          Total EGOLD Mined :
                          <span className="tstatusspan">
                            {minestatData.loading === true
                              ? " ...."
                              : minestatData.data.egold_mined
                              ? parseFloat(
                                  minestatData.data.egold_mined
                                ).toFixed(5)
                              : parseFloat(0).toFixed(5)}{" "}
                            EGOLD
                          </span>
                          <br />
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="card profilecard mb-4 pb-3 mb-md-0">
                      <div className="card-body tstatuscbody">
                        <p className="mb-4 headstatusr">Trading Status</p>

                        <p
                          className="mt-4 mb-1"
                          style={{
                            fontSize: "0.77rem",
                          }}
                        >
                          Total EGOLD Trades :{" "}
                          <span className="tstatusspan">
                            {tradestatData.loading == true
                              ? "..."
                              : parseInt(tradestatData.data.count)}{" "}
                            Trades
                          </span>
                        </p>
                        <p
                          className="mt-4 mb-1"
                          style={{
                            fontSize: "0.77rem",
                          }}
                        >
                          Total EGOLD Sold :{" "}
                          <span className="tstatusspan">
                            {tradestatData.loading == true
                              ? "..."
                              : parseFloat(tradestatData.data.scount).toFixed(
                                  5
                                )}{" "}
                            EGOLD
                          </span>
                        </p>
                        <p
                          className="mt-4 mb-1"
                          style={{
                            fontSize: "0.77rem",
                          }}
                        >
                          Total EGOLD Bought :{" "}
                          <span className="tstatusspan">
                            {tradestatData.loading == true
                              ? "..."
                              : parseFloat(tradestatData.data.bcount).toFixed(
                                  5
                                )}{" "}
                            EGOLD
                          </span>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
}
