import React from "react";
import "./index.css";
import { ReactNotifications } from "react-notifications-component";
import "react-notifications-component/dist/theme.css";
import ls from "local-storage";
import ProtectedRoute from "./routes/protectedRoutes";

import "./App.css";
import Footer from "./components/Footer";
import MainLanding from "./components/mainLanding";
import Profile from "./components/Profile/Profile";
import Security from "./components/Profile/Security";
import History from "./components/Profile/History";
import Transaction from "./components/Profile/Transaction";
import Wallet from "./components/Profile/Wallet";
import Deposit from "./components/Profile/Deposit";
import Withdraw from "./components/Profile/Withdraw";
import Changepw from "./components/Profile/Changepw";
import StakeEgold from "./components/StakeEgold"
import UnStakeEgold from "./components/UnstakeEgold"
import ErrorPage from "./components/ErrorPage";
import { Web3ReactProvider } from "@web3-react/core";
import { ethers } from "ethers";
import { createRoot } from "react-dom/client";
import { createBrowserRouter, RouterProvider, Route } from "react-router-dom";
import { StrictMode } from "react";
import Web3 from "web3";

require("dotenv").config();

//To disable log on production
if (process.env.NODE_ENV === "production") {
  console.log = () => { };
  console.error = () => { };
  console.debug = () => { };
}

const router = createBrowserRouter([
  // {
  //   path: "/",
  //   element: (
  //     <>
  //       <MainLanding />
  //       <Footer />
  //     </>
  //   ),
  //   errorElement: <ErrorPage />,
  // },

  {
    path: "/",
    element: (
      <>
        <MainLanding />
        <Footer />
      </>
    ),
    errorElement: <ErrorPage />,
  },
  {
    path: "/stake",
    element: (
      <>
        <StakeEgold />
      </>
    ),
    errorElement: <ErrorPage />,
  },

  {
    path: "/unstake",
    element: (
      <>
        <UnStakeEgold />
      </>
    ),
    errorElement: <ErrorPage />,
  },

  {
    path: "profile",
    element: (
      <ProtectedRoute>
        <Profile />
      </ProtectedRoute>
    ),
  },
  {
    path: "security",
    element: (
      <ProtectedRoute>
        <Security />
      </ProtectedRoute>
    ),
  },
  {
    path: "transactions",
    element: (
      <ProtectedRoute>
        <History />
      </ProtectedRoute>
    ),
  },
  {
    path: "transactions/:id",
    element: (
      <ProtectedRoute>
        <Transaction />
      </ProtectedRoute>
    ),
  },
  {
    path: "wallet",
    element: (
      <ProtectedRoute>
        <Wallet />
      </ProtectedRoute>
    ),
  },
  {
    path: "deposit/:type",
    element: (
      <ProtectedRoute>
        <Deposit />
      </ProtectedRoute>
    ),
  },
  {
    path: "withdraw/:type",
    element: (
      <ProtectedRoute>
        <Withdraw />
      </ProtectedRoute>
    ),
  },
  {
    path: "changepw",
    element: (
      <ProtectedRoute>
        <Changepw />
      </ProtectedRoute>
    ),
  },
]);

const getLibrary = (provider) => {
  return new Web3(provider);
};
const rootElement = document.getElementById("root");
const root = createRoot(rootElement);

root.render(
  <StrictMode>
    <Web3ReactProvider getLibrary={getLibrary}>
      <ReactNotifications />
      <RouterProvider router={router} />
    </Web3ReactProvider>
  </StrictMode>
);
